import { useState, useEffect, useCallback } from 'react'
import './movie-grid.scss'

import tmdbApi, { category, movieType, tvType } from '../../api/tmdbApi'

import MovieCard from '../movie-card/MovieCard'
import { useNavigate, useParams } from 'react-router-dom'
import Button, { OutlineButton } from '../button/Button'
import Input from '../input/Input'

const MovieGrid = (props) => {
  const [items, setItems] = useState([])
  const [page, setPage] = useState(1)
  const [totalPage, setTotalPage] = useState(0)

  const { keyword } = useParams()
  useEffect(() => {
    const getList = async () => {
      try {
        let response = null

        if (keyword === undefined) {
          const params = {}
          switch (props.category) {
            case category.movie:
              response = await tmdbApi.getMovieList(movieType.popular, {
                params,
              })
              break
            default:
              response = await tmdbApi.getTvList(tvType.popular, { params })
          }
        } else {
          const params = {
            query: keyword,
          }
          response = await tmdbApi.search(props.category, { params })
        }
        // console.log(response.results)
        setItems(response.results)
        setTotalPage(response.total_pages)
      } catch (err) {
        console.log(err)
      }
    }
    getList()
  }, [props.category, keyword])

  const loadMore = async () => {
    try {
      let response = null

      if (keyword === undefined) {
        const params = {
          page: page + 1,
        }
        switch (props.category) {
          case category.movie:
            response = await tmdbApi.getMovieList(movieType.popular, {
              params,
            })
            break
          default:
            response = await tmdbApi.getTvList(tvType.popular, { params })
        }
      } else {
        const params = {
          page: page + 1,
          query: keyword,
        }
        response = await tmdbApi.search(props.category, { params })
      }
      // console.log(response.results)
      setItems([...items, ...response.results])
      setPage(page + 1)
    } catch (err) {
      console.log(err)
    }
  }

  return (
    <>
      <MovieSearch category={props.category} keyword={keyword} />
      <div className="movie-grid">
        {items.map((item, index) => (
          <MovieCard key={index} category={props.category} item={item} />
        ))}
      </div>

      {page < totalPage ? (
        <div className="movie-grid__loadmore">
          <OutlineButton className="small" onClick={loadMore}>
            Load more...
          </OutlineButton>
        </div>
      ) : null}
    </>
  )
}

const MovieSearch = (props) => {
  const navigate = useNavigate()

  const [keyword, setKeyword] = useState(props.keyword ? props.keyword : '')

  const goToSearch = useCallback(() => {
    if (keyword.trim().length > 0) {
      navigate(`/${category[props.category]}/search/${keyword}}`)
    }
  }, [keyword, props.category, navigate])

  useEffect(() => {
    const enterSearchEvent = (e) => {
      e.preventDefault()
      if (e.keyCode === 13) {
        goToSearch()
      }
    }
    document.addEventListener('keyup', enterSearchEvent)
    return () => {
      document.removeEventListener('keyup', enterSearchEvent)
    }
  }, [keyword, goToSearch])

  return (
    <div className="movie-search">
      <Input
        type="text"
        placeholder="Search movie..."
        value={keyword}
        onChange={(e) => setKeyword(e.target.value)}
      />

      <Button className="small" onClick={goToSearch}>
        Search
      </Button>
    </div>
  )
}

export default MovieGrid
